<template>
  <!-- <pre>{{ form }}</pre> -->
  <h1>Form UI</h1>
  <h2>Add user</h2>
  <form class="form" @submit.prevent="sendUser">
    <label for="name"> Имя (Например: Paul)</label
    ><input id="name" type="text" v-model="form.name.value" />
    <label for="date"> Дата рождения (Например: 12.31.2000 / 12/02/2021)</label
    ><input id="date" type="text" v-model="form.date.value" />
    <label for="email"> Email </label
    ><input id="email" type="email" v-model="form.email.value" />
    <label for="tel"> Телефон (Например: +1 (999) 999-9999</label
    ><input id="tel" type="text" v-model="form.tel.value" />
    <input type="submit" :disabled="!form.valid" value="Отправить" />
  </form>
  <h2>Edit / Delete user</h2>
  <div>
    <label for="editUserID">ID пользователя</label
    ><input id="editUserID" type="text" v-model="userID" />
    <button @click="removeUser">Удалить</button>
    <button @click="openEditForm">
      {{ isEdit ? "Закрыть редактирование" : "Открыть редактирование" }}
    </button>
    <form @submit.prevent="editUser" v-if="isEdit">
      <label for="editName"> Имя </label
      ><input id="editName" type="text" v-model="editForm.name.value" />
      <label for="editDate"> Дата рождения </label
      ><input id="editDate" type="text" v-model="editForm.date.value" />
      <label for="editMail"> Email </label
      ><input id="editMail" type="email" v-model="editForm.email.value" />
      <label for="editPhone"> Телефон</label
      ><input id="editPhone" type="text" v-model="editForm.tel.value" />
      <input type="submit" value="Отправить" />
    </form>
  </div>
</template>

<script>
/* eslint-disable no-unused-vars */
import useForm from "@/use/form.js";
import useField from "@/use/field.js";
import useValidators from "@/use/validators.js";
import fb from "firebase/app";
import "firebase/database";
import { ref } from "vue";

const { required, email, phone, name, date } = useValidators();

class User {
  constructor(userForm) {
    this.name = userForm.name.value;
    this.email = userForm.email.value;
    this.date = userForm.date.value;
    this.tel = userForm.tel.value;
  }
}

export default {
  name: "App",
  setup() {
    const initialUser = {
      name: {
        value: "",
        validators: { required, name },
      },
      email: {
        value: "",
        validators: { required, email },
      },
      date: {
        value: "",
        validators: { required, date },
      },
      tel: {
        value: "",
        validators: { required, phone },
      },
    };
    const form = useForm(initialUser);
    const editForm = useForm(initialUser);
    const isEdit = ref(false);
    let userID = ref("-MXCtCj3HVjruYdAl7ew");

    const resetForm = (form) => {
      form.name = useField(initialUser.name);
      form.email = useField(initialUser.email);
      form.tel = useField(initialUser.tel);
      form.date = useField(initialUser.date);
    };

    const sendUser = () => {
      let user = new User(form);
      fb.database()
        .ref("/user")
        .push(user)
        .then((e) => {
          fetch(
            "https://api.telegram.org/bot1744663489:AAFs4hxDZazPdCt-tMZbj5hA9AoyLlKG_6Q/sendMessage?chat_id=578668310&text=Новый пользователь! " +
              e.key
          );
        })
        .finally(() => resetForm(form));
    };
    const editUser = () => {
      let user = new User(editForm);
      if (
        fb
          .database()
          .ref("/user/" + userID.value)
          .isEqual(null)
      ) {
        console.log("uuu");
        return;
      }
      fb.database()
        .ref("/user/" + userID.value)
        .set(user)
        .finally(() => {
          resetForm(editForm);
          userID.value = "";
        });
    };
    const removeUser = () => {
      fb.database()
        .ref("/user/" + userID.value)
        .remove()
        .then(() => (userID.value = ""));
    };
    const openEditForm = () => {
      isEdit.value = !isEdit.value;
    };

    return {
      form,
      sendUser,
      isEdit,
      openEditForm,
      editForm,
      editUser,
      userID,
      removeUser,
    };
  },
};
</script>

<style>
.form {
  display: flex;
  flex-direction: column;
}
</style>