export default function useValidators() {
  const email = (email) => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  const phone = phone => {
    const re = /^((\+1)+\s+(\([0-9]{3}\))+\s+([0-9]){3}[\s-]+([0-9]{4}))$/;
    return re.test(String(phone))
  }

  const name = name => {
    const re = /(-?([A-Z].\s)?([A-Z][a-z]+)\s?)+([A-Z]'([A-Z][a-z]+))?/g
    return re.test(String(name))
  }

  /* eslint-disable no-useless-escape */
  const date = date => {
    const re = /^(0?[1-9]|1[012])([.\/\-])(0?[1-9]|[12][0-9]|3[01])\2([0-9][0-9][0-9][0-9])(([ -])([0-1]?[0-9]|2[0-3]):[0-5]?[0-9]:[0-5]?[0-9])?$/
    return re.test(String(date))
  }

  const required = (value) => !!value;

  return { required, email, phone, name, date }
}