import { reactive, ref } from "@vue/reactivity"
import { watch } from "@vue/runtime-core";

export default function useField(field) {
  const isValid = ref(true);
  const value = ref(field.value);
  const touched = ref(false);
  const errors = reactive({});

  // val - input value
  const reassign = (val) => {
    isValid.value = true;
    Object.keys(field.validators ?? {})
      .map(k => {
        const valid = field.validators[k](val)
        errors[k] = !valid
        if(!valid) {
          isValid.value = false;
        }
      });
    Object.keys(field.limiters ?? {})
      .map(k => {
        const validValue = field.limiters[k](val)
        value.value = validValue
      })
  }
  watch(value, reassign)
  reassign(value.value);
  
  return { isValid, value, errors, touched, blur: () => touched.value = true }
}