import { reactive } from "@vue/reactivity";
import { computed } from "@vue/runtime-core";
import useField from "./field";
import useValidators from "./validators";


const SAME_OPTIONS = 'options_same'

export default function useForm(init = {}) {
  const form = reactive({});

  for(const [key, value] of Object.entries(init)) {
    if(key.includes('same')) continue;
    form[key] = useField(value)
  }

  // helper
  const notIncluded = (k) => {
    return !['valid','sameFields', SAME_OPTIONS].includes(k);
  }

  form.valid = computed(() => {
    return Object.keys(form)
      .filter(notIncluded)
      .every(item => form[item].isValid)
  })
  
  if(SAME_OPTIONS in init) {
    const { isEqualFields } = useValidators();

    form.sameFields = {}

    for(let key of init[SAME_OPTIONS]) {
      const isValid = computed(() => {
        return isEqualFields(form[key[0]].value, form[key[1]].value)
      })
      form.sameFields[key] = isValid
    }

    form.sameFields.valid = computed(() => {
      return Object.keys(form.sameFields).filter(k => k !== 'valid').every(item => form.sameFields[item])
    })
  }

  return form
}